import React, { useState } from 'react';
import loginService from '../services/login';
import contactsService from '../services/contacts';

const LoginForm = ({ setUser, setError }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleUsernameInput = (e) => {
    setUsername(e.target.value);
  }

  const handlePasswordInput = (e) => {
    setPassword(e.target.value);
  }

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const user = await loginService.login({ username, password });
      window.localStorage.setItem('loggedInContactsUser', JSON.stringify(user));

      contactsService.setToken(user.token);
      setUser(user);
      setUsername('');
      setPassword('');
    } catch (err) {
      setError(err);
      setTimeout(() => {
        setError(null)
      }, 5000)
    }
  }

  return (
    <>
      <form onSubmit={handleLogin}>
        <div>
          Username
          <input type='text' value={username} name='Username' onChange={handleUsernameInput} />
        </div>
        <div>
          Password
          <input type='text' value={password} name='Password' onChange={handlePasswordInput} />
        </div>
        <button type='submit'>Submit</button>
      </form>
    </>
  )
}

export default LoginForm;