import React from 'react';
import Contact from './Contact.js';

const ContactDisplay = ({ allContacts, setContacts, filterBy, setError, children }) => {
  let contactsToShow = allContacts.filter(contact => {
    return `${contact.firstName.toLowerCase()} ${contact.lastName.toLowerCase()}`.includes(filterBy.toLocaleLowerCase())
  });

  return (
    <div className='contact_display'>
      <h2>List:</h2>
      {children}
      {contactsToShow.map(contact => 
        <Contact key={contact.firstName} contact={contact} setContacts={setContacts} allContacts={allContacts} setError={setError}></Contact>
      )}
    </div>
  );
}

export default ContactDisplay