import React from 'react';
import { useState, useEffect } from 'react';
import contactsService from './services/contacts';
import './App.css';
import ContactDisplay from './components/ContactDisplay';
import ErrorDisplay from './components/ErrorDisplay';
import LoginForm from './components/LoginForm';
import ContactForm from './components/ContactForm';
import Togglable from './components/Togglable';
import LoginInfo from './components/LoginInfo';
import Search from './components/Search';

function App() {
  const [allContacts, setContacts] = useState([]);
  const [error, setError] = useState(null);
  const [loginVisible, setLoginVisible] = useState(false);
  const [user, setUser] = useState(null);
  const [filterBy, setFilterBy] = useState('');

  useEffect(() => {
    contactsService.getAll()
                   .then(contacts => setContacts(contacts));
  }, []);

  useEffect(() => {
    const loggedInUserJSON = window.localStorage.getItem('loggedInContactsUser');
    if (loggedInUserJSON) {
      const user = JSON.parse(loggedInUserJSON);
      setUser(user);
      contactsService.setToken(user.token);
    }
  }, []);

  return (
    <div className="App">
      <h1>Contacts</h1>
      <ErrorDisplay error={error}></ErrorDisplay>
      {
      user === null ? 
      <Togglable buttonLabel='Login'>
        <LoginForm setUser={setUser} setError={setError}></LoginForm>
      </Togglable>  : 
      <>
        <LoginInfo user={user} setUser={setUser}></LoginInfo>
        <Togglable buttonLabel='Add Contact'>
          <ContactForm setContacts={setContacts} allContacts={allContacts} setError={setError}></ContactForm>
        </Togglable>
        <ContactDisplay setContacts={setContacts} allContacts={allContacts} filterBy={filterBy} setError={setError}>
          <Search setFilterBy={setFilterBy}></Search>
        </ContactDisplay>
      </>
      }
    </div>
  );
}

export default App;
