import React, { useState } from 'react';

const Search = ({ value, setFilterBy }) => {

  const handleSearchInput = (event) => {
    let query = event.target.value;
    setFilterBy(query);
  }

  return (
    <div>
      Search: <input type='text' onChange={handleSearchInput} value={value}></input>
    </div>
  )
}

export default Search